*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

body {
  margin: 0;
  display: grid;
  min-width: 320px;
  min-height: 100vh;
  grid-template-rows: min-content 1fr min-content;
  font-family: "Open Sans", "Arial, sans-serif"; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.page-header {
  position: absolute;
  width: 100%;
  padding-top: 21px; }
  @media (max-width: 1023px) {
    .page-header {
      padding-top: 0; } }
  .page-header--nojs {
    position: static; }
    .page-header--nojs .page-header__toggle {
      display: none; }

.page-header__nav {
  max-width: 1166px;
  width: 100%;
  margin: 0 auto; }

.page-header__toggle {
  display: none;
  width: 50px;
  height: 50px;
  padding: 0;
  border: none;
  background-color: #0ad9c6;
  background-image: url("../../img/menu-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer; }
  @media (max-width: 1023px) {
    .page-header__toggle {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 1;
      display: block; } }
  .page-header__toggle--opened {
    background-color: #ffffff;
    background-image: url("../../img/close-icon.svg"); }

.main-nav {
  display: flex;
  justify-content: center; }

.menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 874px;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  list-style: none; }
  @media (max-width: 1023px) {
    .menu {
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      padding-top: 100px;
      background-color: #ffffff;
      z-index: 1; }
      .menu .menu__item {
        position: relative;
        display: flex;
        align-items: center;
        margin-bottom: 30px;
        padding-top: 17px;
        padding-bottom: 17px;
        padding-left: 16px;
        padding-right: 16px;
        background-color: #f6f6f6; }
        .menu .menu__item::after {
          content: '';
          display: block;
          margin-left: auto;
          width: 25px;
          height: 25px;
          background-image: url("../../img/icon-arrow.svg");
          background-repeat: no-repeat;
          background-position: center;
          opacity: 0.5; }
      .menu .menu__link {
        color: #48494d; }
      .menu--opened {
        display: block; }
      .menu--closed {
        display: none; }
      .menu--nojs {
        padding-top: 0;
        position: static;
        display: block; } }

.menu__item {
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 9px; }

.menu__link {
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: #ffffff;
  opacity: 0.7; }
  .menu__link:hover, .menu__link:focus {
    opacity: 1; }

